<template>
<div class="card">
    <h3 class="card-header">{{ categoryName }}</h3>
    <div class="card-body">
        <div class="accordion" id="accordionCharacter">
            <div v-for="(index, pname) in data" :key="pname">
                <oracle v-if="Array.isArray(data[pname])" :data="data[pname]" :dtitle="pname" :dname="pname" cat="accordionCharacter"/>
                <oracle-category v-if="!Array.isArray(data[pname])" :categoryName="pname" :data="data[pname]"/>
            </div>
        </div>    
  </div>
</div>
</template>

<script>
import Oracle from "./Oracle";

export default {
    name: "OracleCategory",
    components: {Oracle},
    props: ['categoryName', 'data', 'abs']
}
</script>

